import React from 'react';

interface SupporterDetailPanelProps {
  supporter: any;
  isOpen: boolean;
  onClose: () => void;
}

const SupporterDetailPanel: React.FC<SupporterDetailPanelProps> = ({ supporter, isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-y-0 right-0 w-96 bg-white border-l shadow-xl z-50">
      <div className="p-6">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold">{supporter?.name || 'Supporter Details'}</h2>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">×</button>
        </div>
        <p>Full CRM detail panel - To be implemented</p>
      </div>
    </div>
  );
};

export default SupporterDetailPanel;